import React from "react";
import { Body1 } from "@material/react-typography";

interface MeetingBlockProps {
  start: number;
  duration: number;
  displayText: string;
}

const MeetingBlock: React.SFC<MeetingBlockProps> = (
  props: MeetingBlockProps
) => (
  <div
    style={{
      position: "absolute",
      top: props.start * 100 + "%",
      height: props.duration * 100 + "%",
      width: "100%",
      borderRadius: 3,
      background: "red"
    }}
  >
    <Body1>{props.displayText}</Body1>
  </div>
);

export default MeetingBlock;
